<template>
  <div class="pageContent">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <el-tabs v-model="searchList.tabType" @tab-click="getData">
        <el-tab-pane label="出售中" name="1"></el-tab-pane>
        <el-tab-pane label="待出售" name="0"></el-tab-pane>
        <el-tab-pane label="已下架" name="2"></el-tab-pane>
        <el-tab-pane label="回收站" name="3"></el-tab-pane>
      </el-tabs>
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right" @click="getData" type="primary" size="small">查询</el-button>
        <el-button style="float: right;margin-right: 15px" @click="reset" size="small">重置</el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="商品名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="商品名称" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="商品类目：">
            <el-cascader
              v-model="searchList.shopCategoryIdArr"
              :options="category"
              :props="{ checkStrictly: true }"
              clearable></el-cascader>
          </el-form-item>
          <el-form-item label="商品编码：">
            <el-input style="width: 203px" v-model="searchList.code" clearable placeholder="商品编码" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="规格编码：">
            <el-input style="width: 203px" v-model="searchList.skuCode" clearable placeholder="规格编码" @input="change($event)"></el-input>
          </el-form-item>
          <el-form-item label="物流服务：">
            <el-select v-model="searchList.companyType" clearable placeholder="请选择物流服务">
              <el-option label="包邮" value="0"></el-option>
              <el-option label="不包邮" value="1"></el-option>
              <el-option label="到店自提" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="库存预警：">
            <el-select v-model="searchList.productWarnType" clearable placeholder="请选择库存状态">
              <el-option label="已售罄（当前库存为0）" value="0"></el-option>
              <el-option label="已滞销（销量为0）" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 - head-->
    <el-card class="operate-container mt10" shadow="never">
      <div class="flex-row-between" style="width:100%;">
        <div>
          <i class="el-icon-tickets"></i>
          <span>数据列表</span>
          <span style="margin-left:40px;color:#E6A23C;">温馨提示：如果一个商品是多规格，成本价取最低值，销售价取该商品最高值，库存为该商品所有库存之和；</span>
        </div>
        <div style="padding-right:20px;">
          <el-button type="text" icon="el-icon-circle-plus-outline" @click="$router.push({path:'/commodity/addCommodity'})">新增商品</el-button>
        </div>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <div>
      <el-card style="margin-top:20px;" class="filter-container" shadow="never">
        <el-table
          ref="multipleTable"
          :data="list"
          tooltip-effect="dark"
          style="width: 100%"
          @sort-change='tableSortChange'
          @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="商品信息" width="400">
            <template slot-scope="scope">
              <div class="flex-row-start">
                <el-image style="width: 100px; height: 100px" :src="scope.row.imageUrl" fit="fill"></el-image>
                <span style="flex:1;margin-left:10px;">{{ scope.row.name }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="productCategoryShopName" label="分类"></el-table-column>
          <el-table-column prop="activityName" label="活动状态"></el-table-column>
          <el-table-column label="物流服务" prop="companyNane"></el-table-column>
          <el-table-column prop="sumStock" label="库存"></el-table-column>
          <el-table-column label="成本价" sortable="minOriginPrice">
            <template slot-scope="scope">
              {{scope.row.minOriginPrice ? scope.row.minOriginPrice : '-'}}
            </template>
          </el-table-column>
          <el-table-column prop="maxPrice" label="销售价" sortable="maxPrice"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button v-if="searchList.tabType == 3" type="text" size="small" @click="restoreItem(scope.row,1)">恢复至已下架</el-button>
              <el-button v-if="searchList.tabType == 3" type="text" size="small" @click="restoreItem(scope.row,0)">恢复至出售中</el-button>
              <el-button v-if="searchList.tabType != 1 && searchList.tabType != 3" type="text" size="small" @click="salesStatuItem(scope.row,0)">上架</el-button>
              <el-button v-if="searchList.tabType == 1" type="text" size="small" @click="salesStatuItem(scope.row,1)">下架</el-button>
              <el-button type="text" size="small" @click="$router.push({path:'/commodity/commodityList/commodityInfo',query:{productId:scope.row.id}})">查看详情</el-button>
              <el-button v-if="searchList.tabType != 3 && searchList.tabType != 1" type="text" size="small" @click="$router.push({path:'/commodity/commodityList/editCommodity',query:{productId:scope.row.id}})">编辑</el-button>
              <el-button style="color:#909399;" v-if="searchList.tabType == 0 || searchList.tabType == 2 " type="text" size="small" @click="deleteItem(scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="flex-row-between" style="padding-top:20px;">
          <div class="flex-row-start">
            <div class="flex-row-start">
              <el-checkbox @change="selectAllSureList" v-model="selectAllSure">当前页全选</el-checkbox>
              <div class="checkBoxDiv">
                已选择 <span style="color:#409EFF">{{multipleSelection.length}}</span> 项
              </div>
            </div>
            <div class="flex-row-start" style="margin-left:20px">
              <el-button v-if="searchList.tabType != 1 && searchList.tabType != 3" type="primary" @click="salesStatuAll(0)">批量上架</el-button>
              <el-button v-if="searchList.tabType == 1" type="primary" @click="salesStatuAll(1)">批量下架</el-button>
              <el-button v-if="searchList.tabType == 1 || searchList.tabType == 2" type="primary" @click="deleteAll">批量删除</el-button>
              <el-button type="primary" @click="changeClass">批量改分类</el-button>
              <el-button v-if="searchList.tabType == 3" type="primary" @click="restoreALl(1)">批量恢复至已下架</el-button>
              <el-button v-if="searchList.tabType == 3" type="primary" @click="restoreALl(0)">批量恢复至出售中</el-button>
            </div>
          </div>
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :page-sizes="[5,10,15,20]"
              :page-size="searchList.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="listTotal">
            </el-pagination>
          </div>
        </div>
      </el-card>
    </div>
    <!-- 批量改分类弹出框 -->
    <el-dialog title="请选择新分类" :visible.sync="showClass" width="40%" >
      <div>
        已选择<span style="color:#F56C6C;">{{multipleSelection.length}}</span>条商品
      </div>
      <div>
        <el-form :model="ruleForm" ref="ruleForm" label-width="140px" class="demo-ruleForm">
          <el-form-item label="商品分类：" required>
            <el-cascader
            v-model="ruleForm.shopCategoryIdsArr"
            :options="category"
            :props="props"
            clearable></el-cascader>
          </el-form-item>
          <div class="flex-row-end">
            <el-button :loading="changeClassLoading" type="primary" @click="changeClassFun">确 定</el-button>
            <el-button @click="showClass = false">取 消</el-button>
          </div>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getProductCategoryShopAllList } from '@/api/commodity/commodityClassify'
import { 
  getProductInfoList,
  updateSalesStatus,
  deleteProduct,
  restoreDeleteStatus,
  updateProductCategoryShop,
  checkProduct
} from '@/api/commodity/commodityList'
export default {
  data(){
    return{
      searchList:{
        tabType:'1',
        shopCategoryIdArr:[],
        pageNum:1,
        pageSize:5,
        productWarnType:''
      },
      category:[],
      list:[],
      listTotal:0,
      multipleSelection:[],           // 勾选的数据
      selectAllSure:false,            // 列表全选
      showClass:false,
      ruleForm:{},
      props: { multiple: true },
      changeClassLoading:false,
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    console.log(typeof this.$route.query.productWarnType)
    if(this.$route.query.productWarnType) this.searchList.productWarnType = this.$route.query.productWarnType
    if(this.$route.query.tabType) this.searchList.tabType = this.$route.query.tabType+''
    this.getData();
    this.getCategory()
  },
  methods:{
    tableSortChange (column) {
      console.log(column.column)
      this.searchList.sort = column.column.sortable
      if (column.column.order == "ascending") {
        this.searchList.direction = 'asc'
      }
      if (column.column.order == "descending") {
        this.searchList.direction = 'desc'
      }
      this.getData()
    },
    // 列表 - 获取数据
    getData(){
      this.searchList.shopCategoryId = this.searchList.shopCategoryIdArr[1] || ''
      this.searchList.shopCategoryPid = this.searchList.shopCategoryIdArr[0] || ''
      console.log('请求的数据',this.searchList)
      getProductInfoList(this.searchList).then(res=>{
        console.log('数据列表',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    // 批量 - 分类
    changeClass(){
      if(this.multipleSelection.length == 0){
        this.$message({
          message: '请选择商品后再试',
          type: 'warning'
        });
        return;
      }
      this.showClass = true
    },
    changeClassFun(){
      console.log(this.ruleForm)
      if(!this.ruleForm.shopCategoryIdsArr){
        this.$message.error('请选择分类后再试');
        return;
      }
      this.changeClassLoading = true;
      let prr = [];
      this.multipleSelection.forEach(e => prr.push(e.id))
      let arr = [];
      this.ruleForm.shopCategoryIdsArr.forEach(e=>{
        let obj = {
          pid:e[0],
          id:e[1]
        }
        arr.push(obj)
      }) 
      let obj = {
        shopCategoryIdArray:arr,
        productId:prr
      }
      console.log('arr',arr)
      updateProductCategoryShop(obj).then(res=>{
        console.log('批量改分类',res)
        if(res.code == 200){
          this.$message({
            message: '修改成功',
            type: 'success'
          });
          this.getData()
          this.showClass = false;
          setTimeout(()=>{
            this.changeClassLoading = false;
          },1000)
        }else{
          this.changeClassLoading = false;
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 恢复
    restoreItem(e,t){
      this.$confirm(`确认将该商品恢复到${t == 0 ? '出售中' : '下架'}吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.restoreFun(e.id,t)
      }).catch(() => {});
    },
    // 批量 - 恢复
    restoreALl(t){
      if(this.multipleSelection.length == 0){
        this.$message({
          message: '请选择商品后再试',
          type: 'warning'
        });
        return;
      }
      this.$confirm(`确认将该商品移入回收站吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let arr = [];
        this.multipleSelection.forEach(e => arr.push(e.id))
        this.restoreFun(arr.join(','),t)
      }).catch(() => {});
    },
    // 接口 - 恢复
    restoreFun(e,t){
      restoreDeleteStatus(e,t,this.shop.shopId).then(res=>{
        console.log('恢复',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 列表 - 删除
    deleteItem(e){
      this.$confirm(`确认将该商品移入回收站吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteProduct(e.id)
      }).catch(() => {});
    },
    // 批量 - 删除
    deleteAll(){
      if(this.multipleSelection.length == 0){
        this.$message({
          message: '请选择商品后再试',
          type: 'warning'
        });
        return;
      }
      let ids = [];
      this.multipleSelection.forEach(e => ids.push(e.id))
      if(this.searchList.tabType == 1){
        console.log('下架检测')
        checkProduct({ids,operateType:2}).then(res=>{
          console.log('下架检测',res)
          if(res.code == 200){
            this.$confirm(`确认将该商品移入回收站吗?`, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.deleteProduct(ids.join(','))
            }).catch(() => {});
          }else{
            this.$message.error(res.message);
          }
        })
        return;
      }
      this.$confirm(`确认将该商品移入回收站吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteProduct(ids.join(','))
      }).catch(() => {});
    },
    // 接口 - 删除
    deleteProduct(ids){
      deleteProduct(ids).then(res=>{
        console.log('商品删除',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 底部固定 - 全选
    selectAllSureList(){
      if (this.selectAllSure) {
        this.selectAllSure = false;
        this.$refs.multipleTable.toggleAllSelection();
      } else {
        this.selectAllSure = true;
        this.$refs.multipleTable.clearSelection();
      }
      console.log('multipleSelection',this.multipleSelection)
    },
    // 列表 - 上、下架
    salesStatuItem(e,i){
      if(i == 1){
        console.log('下架检测')
        checkProduct({ids:[e.id],operateType:1}).then(res=>{
          console.log('下架检测',res)
          if(res.code == 200){
            this.$confirm(`确认将该商品下架吗?`, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.updateSalesStatus(e.id,i)
            }).catch(() => {});
          }else{
            this.$message.error(res.message);
          }
        })
        return;
      }
      this.$confirm(`确认将该商品上架吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.updateSalesStatus(e.id,i)
      }).catch(() => {});
    },
    // 批量 - 上、下架
    salesStatuAll(i){
      if(this.multipleSelection.length == 0){
        this.$message({
          message: '请选择商品后再试',
          type: 'warning'
        });
        return;
      }
      let ids = [];
      this.multipleSelection.forEach(e => ids.push(e.id))
      if(this.searchList.tabType == 1){
        console.log('出售中点击的')
        checkProduct({ids,operateType:1}).then(res=>{
          console.log('下架检测',res)
          if(res.code == 200){
            this.$confirm(`确认将该商品下架吗?`, '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              this.updateSalesStatus(e.id,i)
            }).catch(() => {});
          }else{
            this.$message.error(res.message);
          }
        })
        return;
      }
      this.$confirm(`确认将该商品上架吗?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.updateSalesStatus(ids.join(','),i)
      }).catch(() => {});
    },
    // 接口 - 商品上下架
    updateSalesStatus(ids,type){
      updateSalesStatus(ids,type).then(res=>{
        console.log('商品上下架',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
          this.getData()
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 搜索栏 - 重置
    reset(){
      this.searchList.name = ''
      this.searchList.shopCategoryIdArr = []
      this.searchList.shopCategoryId = ''
      this.searchList.shopCategoryPid = ''
      this.searchList.code = ''
      this.searchList.skuCode = ''
      this.searchList.companyType = ''
      this.searchList.productWarnType = ''
      this.getData()
    },
    // 表单选择
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      this.selectAllSure = (this.multipleSelection.length == this.list.length) && (this.list.length != 0)
    },
    // 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    change(){
      this.$forceUpdate()
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
  .checkBoxDiv{
    font-size: 14px;
    margin-left: 40px;
    line-height: 19px;
    color: #606266;
  }
</style>