import request from '@/utils/request'
// 查询商品列表
export function getProductInfoList(data) {
  return request({
    url: `/product/business/product/getProductInfoList`,
    method: 'post',
    data: data
  })
}
// 商品上下架
export function updateSalesStatus(ids,type) {
  return request({
    url: `/product/business/product/updateSalesStatus?id=${ids}&salesStatus=${type}`,
    method: 'get',
  })
}
// 商品删除
export function deleteProduct(id) {
  return request({
    url: `/product/business/product/deleteProduct?id=${id}`,
    method: 'delete',
  })
}
// 商品详情
export function getProductInfoById(id) {
  return request({
    url: `/product/business/product/getProductInfoById?productId=${id}`,
    method: 'get',
  })
}
// 商品上下架
export function restoreDeleteStatus(ids,type,sid) {
  return request({
    url: `/product/business/product/restoreDeleteStatus?productIds=${ids}&salesStatus=${type}&shopId=${sid}`,
    method: 'get',
  })
}
// 更改分类
export function updateProductCategoryShop(data) {
  return request({
    url: `/product/business/product/updateProductCategoryShop`,
    method: 'post',
    data: data
  })
}
// 编辑商品
export function updateProduct(data) {
  return request({
    url: `/product/business/product/updateProduct`,
    method: 'post',
    data: data
  })
}
// 检测商品是否在活动中
export function checkProduct(data) {
  return request({
    url: `/sys/business/shopActivity/checkProduct`,
    method: 'post',
    data: data
  })
}
// 商品组件调取的商品列表
export function getSubgroupProductList(data) {
  return request({
    url: `/product/subgroupProduct/getSubgroupProductList`,
    method: 'post',
    data: data
  })
}
// 商品组件回显
export function getSubgroupProductInfos(id) {
  return request({
    url: `/product/subgroupProduct/getSubgroupProductInfos?productIds=${id}`,
    method: 'get',
  })
}