import request from '@/utils/request'
// 店铺分类列表
export function getProductCategoryInfoList(data) {
  return request({
    url: `/product/business/productCategoryShop/getProductCategoryInfoList`,
    method: 'post',
    data: data
  })
}
// 新增分类
export function insertProductCategoryShop(data) {
  return request({
    url: `/product/business/productCategoryShop/insertProductCategoryShop`,
    method: 'post',
    data: data
  })
}
// 删除分类
export function deleteProductCategoryShop(data) {
  return request({
    url: `/product/business/productCategoryShop/deleteProductCategoryShop`,
    method: 'post',
    data: data
  })
}
// 查询店内分类名称是否存在
export function isCategoryNameExist(data) {
  return request({
    url: `/product/business/productCategoryShop/isCategoryNameExist?name=${data.name}&level=${data.level}&shopId=${data.shopId}&parentId=${data.parentId}`,
    method: 'get',
  })
}
// 更新商品分类排序
export function updateProductCategoryShopOrderNo(data) {
  return request({
    url: `/product/business/productCategoryShop/updateProductCategoryShopOrderNo`,
    method: 'post',
    data: data
  })
}
// 更新分类名称
export function updateProductCategoryShop(data) {
  return request({
    url: `/product/business/productCategoryShop/updateProductCategoryShop`,
    method: 'post',
    data: data
  })
}
// 查询店内分类名称是否存在
export function getProductCategoryShopAllList(id) {
  return request({
    url: `/product/business/productCategoryShop/getProductCategoryShopAllList?shopId=${id}`,
    method: 'get',
  })
}
// 分页查询商品店铺分类
export function getProductCategoryInfoPageList(data) {
  return request({
    url: `/product/business/productCategoryShop/getProductCategoryInfoPageList`,
    method: 'post',
    data: data
  })
}
